label {
  font-size: 3rem;
}

input {
  
}

.to-do {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  border: 1px solid black;
  width: 50%;
}

.to-do-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 50%;
  border-bottom: 1px solid black;
  margin-bottom: 5%;
}

input[type=checkbox]:focus {
  outline: 0;
}

input[type=checkbox] {
  transform: scale(3);
  margin: 5%;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 400px;
}

button, input[type=text] {
  margin: 5px;
  height: 2rem;
  width: auto;
}